"use client"

import { useState } from "react"
import { AdminSidebar } from "@/components/admin/sidebar"
import { AdminTopbar } from "@/components/admin/topbar"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { useAdminBookings } from "@/lib/hooks/use-admin"
import { useSpaces } from "@/lib/hooks/use-spaces"
import { Loader2, CheckCircle, Clock, XCircle, Calendar, User, Building2 } from "lucide-react"
import {
  format,
  startOfWeek,
  addDays,
  startOfMonth,
  endOfMonth,
  eachDayOfInterval,
  isSameDay,
  addMonths,
  subMonths,
} from "date-fns"
import { ChevronLeft, ChevronRight } from "lucide-react"
import { cn } from "@/lib/utils"
import type { BookingStatus } from "@/lib/types"

type ViewMode = "week" | "month"

export default function AdminCalendarPage() {
  const { data: bookings = [], isLoading: bookingsLoading } = useAdminBookings()
  const { data: spaces = [], isLoading: spacesLoading } = useSpaces()
  const [currentDate, setCurrentDate] = useState(new Date())
  const [selectedDate, setSelectedDate] = useState<Date | null>(new Date())
  const [viewMode, setViewMode] = useState<ViewMode>("month")

  if (bookingsLoading || spacesLoading) {
    return (
      <div className="flex min-h-screen bg-background">
        <AdminSidebar />
        <div className="flex-1 flex flex-col">
          <AdminTopbar />
          <main className="flex-1 p-6 flex items-center justify-center">
            <Loader2 className="h-12 w-12 animate-spin text-primary" />
          </main>
        </div>
      </div>
    )
  }

  const getSpaceColor = (spaceType?: string) => {
    switch (spaceType) {
      case "auditorium":
        return "bg-auditorium"
      case "gym":
        return "bg-gym"
      case "soccer":
        return "bg-soccer"
      default:
        return "bg-primary"
    }
  }

  const getStatusColor = (status: BookingStatus) => {
    switch (status) {
      case "approved":
        return "bg-status-approved/20 text-status-approved border-status-approved/30"
      case "pending":
        return "bg-status-pending/20 text-status-pending border-status-pending/30"
      case "rejected":
        return "bg-status-rejected/20 text-status-rejected border-status-rejected/30"
    }
  }

  const getStatusIcon = (status: BookingStatus) => {
    switch (status) {
      case "approved":
        return <CheckCircle className="h-4 w-4" />
      case "pending":
        return <Clock className="h-4 w-4" />
      case "rejected":
        return <XCircle className="h-4 w-4" />
    }
  }

  const weekStart = startOfWeek(currentDate, { weekStartsOn: 1 })
  const weekDays = Array.from({ length: 7 }, (_, i) => addDays(weekStart, i))

  const monthStart = startOfMonth(currentDate)
  const monthEnd = endOfMonth(currentDate)
  const monthDays = eachDayOfInterval({ start: monthStart, end: monthEnd })

  const getBookingsForDate = (date: Date) => {
    const dateStr = format(date, "yyyy-MM-dd")
    return bookings.filter((b) => {
      const bookingDate = typeof b.date === 'string' ? b.date : new Date(b.date).toISOString().split('T')[0];
      return bookingDate === dateStr;
    })
  }

  const getBookingCountForDate = (date: Date) => {
    return getBookingsForDate(date).length
  }

  const selectedDateBookings = selectedDate ? getBookingsForDate(selectedDate) : []

  const navigatePrev = () => {
    if (viewMode === "week") {
      setCurrentDate(addDays(currentDate, -7))
    } else {
      setCurrentDate(subMonths(currentDate, 1))
    }
  }

  const navigateNext = () => {
    if (viewMode === "week") {
      setCurrentDate(addDays(currentDate, 7))
    } else {
      setCurrentDate(addMonths(currentDate, 1))
    }
  }

  return (
    <div className="flex min-h-screen bg-background">
      <AdminSidebar />
      <div className="flex-1 flex flex-col">
        <AdminTopbar />
        <main className="flex-1 p-6">
          <Card className="bg-card border-border">
            <CardHeader className="flex flex-row items-center justify-between">
              <CardTitle>Calendar View</CardTitle>
              <div className="flex items-center gap-4">
                {/* View Toggle */}
                <div className="flex bg-secondary rounded-lg p-1">
                  <Button
                    variant={viewMode === "week" ? "default" : "ghost"}
                    size="sm"
                    onClick={() => setViewMode("week")}
                  >
                    Week
                  </Button>
                  <Button
                    variant={viewMode === "month" ? "default" : "ghost"}
                    size="sm"
                    onClick={() => setViewMode("month")}
                  >
                    Month
                  </Button>
                </div>
                {/* Navigation */}
                <div className="flex items-center gap-2">
                  <Button variant="outline" size="icon" onClick={navigatePrev}>
                    <ChevronLeft className="h-4 w-4" />
                  </Button>
                  <span className="font-medium min-w-[150px] text-center">
                    {viewMode === "week"
                      ? `${format(weekDays[0], "MMM d")} - ${format(weekDays[6], "MMM d, yyyy")}`
                      : format(currentDate, "MMMM yyyy")}
                  </span>
                  <Button variant="outline" size="icon" onClick={navigateNext}>
                    <ChevronRight className="h-4 w-4" />
                  </Button>
                </div>
              </div>
            </CardHeader>
            <CardContent>
              {/* Legend */}
              <div className="flex items-center gap-6 mb-6 flex-wrap">
                <div className="flex items-center gap-2">
                  <div className="w-3 h-3 rounded bg-auditorium" />
                  <span className="text-sm text-muted-foreground">Auditorium</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-3 h-3 rounded bg-gym" />
                  <span className="text-sm text-muted-foreground">Gym</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-3 h-3 rounded bg-soccer" />
                  <span className="text-sm text-muted-foreground">Soccer Fields</span>
                </div>
                <div className="flex items-center gap-4 ml-auto">
                  <div className="flex items-center gap-2">
                    <CheckCircle className="h-4 w-4 text-status-approved" />
                    <span className="text-sm text-muted-foreground">Approved</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <Clock className="h-4 w-4 text-status-pending" />
                    <span className="text-sm text-muted-foreground">Pending</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <XCircle className="h-4 w-4 text-status-rejected" />
                    <span className="text-sm text-muted-foreground">Rejected</span>
                  </div>
                </div>
              </div>

              {viewMode === "week" ? (
                /* Week View */
                <div className="grid grid-cols-7 gap-2">
                  {weekDays.map((day) => {
                    const dayBookings = getBookingsForDate(day)
                    const bookingCount = dayBookings.length
                    const isSelected = selectedDate && isSameDay(day, selectedDate)
                    const isToday = isSameDay(day, new Date())
                    
                    return (
                      <div 
                        key={day.toISOString()} 
                        className="min-h-[200px] cursor-pointer"
                        onClick={() => setSelectedDate(day)}
                      >
                        <div
                          className={cn(
                            "text-center py-2 rounded-t-lg font-medium relative",
                            isToday ? "bg-primary text-primary-foreground" : isSelected ? "bg-primary/20 text-primary" : "bg-secondary",
                          )}
                        >
                          <div className="text-xs text-muted-foreground">{format(day, "EEE")}</div>
                          <div className="text-lg flex items-center justify-center gap-1">
                            {format(day, "d")}
                            {bookingCount > 0 && (
                              <span className={cn(
                                "text-xs px-1.5 py-0.5 rounded-full font-bold",
                                isToday ? "bg-primary-foreground/20 text-primary-foreground" : "bg-primary text-primary-foreground"
                              )}>
                                {bookingCount}
                              </span>
                            )}
                          </div>
                        </div>
                        <div className="border border-border rounded-b-lg p-2 space-y-1 min-h-[150px]">
                          {dayBookings.slice(0, 3).map((booking) => {
                            const space = booking.space
                            return (
                              <div
                                key={booking.id}
                                className={cn(
                                  "px-2 py-1 rounded text-xs text-background font-medium border",
                                  getSpaceColor(space?.type),
                                )}
                                title={`${space?.name} - ${booking.startTime} to ${booking.endTime} (${booking.status})`}
                              >
                                <div className="truncate flex items-center gap-1">
                                  <span className="truncate">{space?.name?.split(" ")[0] || 'Space'}</span>
                                  {getStatusIcon(booking.status)}
                                </div>
                                <div className="opacity-80 text-[10px]">{booking.startTime}</div>
                              </div>
                            )
                          })}
                          {dayBookings.length > 3 && (
                            <div className="text-xs text-muted-foreground text-center py-1">
                              +{dayBookings.length - 3} more
                            </div>
                          )}
                        </div>
                      </div>
                    )
                  })}
                </div>
              ) : (
                /* Month View */
                <div className="grid grid-cols-7 gap-1">
                  {["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"].map((day) => (
                    <div key={day} className="text-center py-2 font-medium text-muted-foreground text-sm">
                      {day}
                    </div>
                  ))}
                  {/* Padding for first week */}
                  {Array.from({ length: (monthStart.getDay() + 6) % 7 }, (_, i) => (
                    <div key={`pad-${i}`} className="min-h-[100px]" />
                  ))}
                  {monthDays.map((day) => {
                    const dayBookings = getBookingsForDate(day)
                    const bookingCount = dayBookings.length
                    const isSelected = selectedDate && isSameDay(day, selectedDate)
                    const isToday = isSameDay(day, new Date())
                    
                    return (
                      <div
                        key={day.toISOString()}
                        className={cn(
                          "min-h-[100px] border rounded-lg p-2 cursor-pointer transition-colors",
                          isToday ? "border-primary bg-primary/5" : isSelected ? "border-primary bg-primary/10" : "border-border hover:border-primary/50",
                        )}
                        onClick={() => setSelectedDate(day)}
                      >
                        <div
                          className={cn(
                            "text-sm font-medium mb-1 flex items-center justify-between",
                            isToday ? "text-primary" : isSelected ? "text-primary" : "text-foreground",
                          )}
                        >
                          <span>{format(day, "d")}</span>
                          {bookingCount > 0 && (
                            <span className={cn(
                              "text-xs px-1.5 py-0.5 rounded-full font-bold min-w-[20px] text-center",
                              isToday ? "bg-primary text-primary-foreground" : isSelected ? "bg-primary text-primary-foreground" : "bg-primary/20 text-primary"
                            )}>
                              {bookingCount}
                            </span>
                          )}
                        </div>
                        <div className="space-y-1">
                          {dayBookings
                            .slice(0, 2)
                            .map((booking) => {
                              const space = booking.space
                              return (
                                <div
                                  key={booking.id}
                                  className={cn(
                                    "px-1 py-0.5 rounded text-xs text-background truncate flex items-center gap-1",
                                    getSpaceColor(space?.type),
                                  )}
                                  title={`${space?.name} - ${booking.startTime} to ${booking.endTime} (${booking.status})`}
                                >
                                  <span className="truncate">{space?.name?.split(" ")[0] || 'Space'}</span>
                                  {getStatusIcon(booking.status)}
                                </div>
                              )
                            })}
                          {dayBookings.length > 2 && (
                            <div className="text-xs text-muted-foreground text-center">
                              +{dayBookings.length - 2} more
                            </div>
                          )}
                        </div>
                      </div>
                    )
                  })}
                </div>
              )}
            </CardContent>
          </Card>
        </main>
      </div>
    </div>
  )
}
